// Base Variables
$colour-primary: theme-color(primary);
$colour-secondary: theme-color(secondary);
$colour-grey: #555555;
$grid-gutter-width: 30px;
$input-padding-horizontal: $grid-gutter-width / 3;
$input-padding-vertical: $grid-gutter-width / 3;

// Linear Gradient Mixin
@mixin horizontal-gradient($from, $to, $stop: 100%) {
   background-color: $from;
   background-image: -moz-linear-gradient(left, $from 0%, $to $stop);
   background-image: -webkit-gradient(linear, left top, right top, color-stop(0%, $from), color-stop($stop, $to));
   background-image: -webkit-linear-gradient(left, $from 0%, $to $stop);
   background-image: -o-linear-gradient(left, $from 0%, $to $stop);
   background: -ms-linear-gradient(left, $from 0%, $to $stop);
   background: linear-gradient(to right, $from 0%, $to $stop);
   filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='$from', endColorstr='$to', GradientType=1);
}

// Material Design Form
form.md {

   .form-row {
      margin-left: -$grid-gutter-width / 2;
      margin-right: -$grid-gutter-width / 2;

      .form-group {
         padding: $grid-gutter-width / 2 $grid-gutter-width / 2 0;
         align-self: flex-end;
      }
   }

   .form-group {
      position: relative;
      padding-top: $grid-gutter-width / 2;
      margin-bottom: $grid-gutter-width;
   }

   &.search-form .form-group:after {
      position:absolute;
      font-size: 0.75rem;
      font-family: "Font Awesome 5 Pro";
      font-weight: 300;
      content: "\f002";
      right:0;
      top: 43%;
   }

   .form-control {
      border: 0;
      border-radius: 0;
      padding: $grid-gutter-width / 3 0;
      background-color: transparent;
      position: relative;
      z-index: 2;
      font-size: 0.9rem;

      &:not(textarea) {
         height: 44px;
      }

      +label {
         position: absolute;
         z-index: 1;
         top: $input-padding-vertical + $grid-gutter-width / 2;
         margin: 0;
         transition: all 0.2s ease-in-out;
         pointer-events: none;
         left: 0;
         right: $input-padding-horizontal;
      }

      &:focus {
         box-shadow: none;
         background-color: transparent;
      }

      &:focus {
         +label {
            color: $colour-primary;
         }
      }

      &:focus,
      &.has-value {

         +label {
            font-size: 0.75rem;
            top: 0;
         }

      }


   }

   .form-row {
      .form-control {
         +label {
            left: $grid-gutter-width / 2 + $input-padding-horizontal;
            right: $grid-gutter-width / 2 + $input-padding-horizontal;
         }
      }

      .line {
         left: $grid-gutter-width / 2;
         right: $grid-gutter-width / 2;
      }
   }

   .line {
      position: absolute;
      display: block;
      bottom: 0;
      left: 0;
      right: 0;
      height: 2px;
      background-color: $colour-primary;

      &:after {
         content: "";
         height: 2px;
         position: absolute;
         display: block;
         background: $colour-primary;
         left: 0;
         right: 0;
         transform: scaleX(0);
         transition: transform 0.2s ease-in-out;
         transform-origin: 0 0;
      }
   }

   .form-control:focus {
      ~.line {
         &:after {
            transform: scaleX(1);
         }
      }
   }

   // Remove Bootstrap 4 height from select
   select.form-control:not([size]):not([multiple]) {
      height: 44px;
   }
}

// Position labels for when javascript is disabled
.no-js {
   form.md {
      .form-control+label {
         font-size: 0.75rem;
         top: 0;
      }
   }
}