.skyvision,
.skytwo {
   #site-navigation {
      &.navbar {
         background: rgb(84,86,91);
background: linear-gradient(0deg, rgba(84,86,91,0) 0%, rgba(84,86,91,1) 100%);
      }
   }
    
   section {
      &#intro {
         padding-top: 3.5rem;
         padding-bottom: 3.5rem;

         @include media-breakpoint-down(md) {
            padding-top: 2.5rem;
            padding-bottom: 2.5rem;
         }

         font-size: 1.5rem;
         @include media-breakpoint-down(md) {
            font-size: 1rem;
         }
      }

      &#keyBenefits {
         background-color: rgba(theme-color(brand-yellow),0.1);
         padding-bottom: 5.5rem;
         h2.section-title {

            &:before {
               background-color: rgba(theme-color(brand-yellow), 0.25);
               z-index: 0;
            }

            &:after {
               z-index: 1;
            }
         }

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         ul.features {
            list-style-type: none;
            margin: 0;
            padding: 0;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;

            li {
               width: 32%;
               // flex: 0 1 32%;
               padding: 2rem;
               margin-bottom: 1.5rem;
               display: flex;
               flex-direction: column;
               align-items: center;
               border: 1px solid theme-color(brand-gold);

               @include media-breakpoint-only(md) {
                  width: 47%;
                  padding: 2rem 1rem;
               }

               @include media-breakpoint-down(sm) {
                  width: 100%;
                  padding: 2rem 1rem;
               }

               .feature-image {
                  width: 125px;
                  margin-bottom: 1rem;
                  text-align: center;
                  position: relative;

                  svg {
                     &#bad-weather-icon {
                        .cls-1 {
                           stroke: darken(theme-color(brand-gold), 10%);
                        }
                        .cls-2 {
                           fill: darken(theme-color(brand-gold), 10%)!important;
                        }
                     }
                     &#a-to-b-icon{
                        .cls-1,
                        .cls-2 {
                           stroke: darken(theme-color(brand-gold), 10%);
                        }
                        .cls-3,
                        .cls-2 {
                           fill: darken(theme-color(brand-gold), 10%);
                        }
                     }
                     &#data-report-icon {
                        .cls-1,
                        .cls-2 {
                           stroke: darken(theme-color(brand-gold), 10%);
                        }
                     }

                     &#heavy-load-icon {

                        .cls-1 {
                           stroke: darken(theme-color(brand-gold), 10%);
                        }
                     }

                     &#inspection-icon {
                        .cls-1 {
                           stroke: darken(theme-color(brand-gold), 10%);
                        }
                     }
                     &#stopwatch-icon {
                        .cls-1,
                        .cls-3 {
                           stroke: darken(theme-color(brand-gold), 10%);
                        }
                        .cls-2 {
                           fill: darken(theme-color(brand-gold), 10%);
                        }
                     }
                  }
               }

               .feature-text {
                  text-align: center;
                  font-size: 0.875rem;
                  width: 100%;
                  color: theme-color(brand-grey);
                  @include media-breakpoint-down(md) {
                     font-size: 1rem;
                  }

                  h3 {
                     font-size: inherit;
                     font-weight: $font-weight-bold;
                     color: theme-color(brand-gold);
                  }
               }

               .feature-action {
                  margin-top: auto;
               }
            }
         }
      }

      &#aerial-systems {
         padding-bottom: 5.5rem;
         position: relative;
         overflow: hidden;

         &:before {
            content: ' ';
            position: absolute;
            top: 0;
            right: -5rem;
            width: 602px;
            height: 100%;
            transform: skew(-20deg);
            background-color: rgba(theme-color(primary), 0.75);
         }

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;

            &:before {
               width: 500px;
            }
         }

         @include media-breakpoint-down(sm) {
            &:before {
               right: -10rem;
               width: 250px;
            }
         }

         h2.section-title {
            color: white;

            &:before {
               background-color: rgba(theme-color(brand-grey-light), 0.25);
               z-index: 0;
            }

            &:after {
               z-index: 1;
            }
         }

         .specs {
            position: relative;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;

            ul {
               list-style-type: none;
               margin: 0;
               padding: 0;
               flex-basis: 1;
               flex-basis: 24.5%;
               z-index: 3;
               @include media-breakpoint-down(sm) {
                  flex-basis: 48%;
               }

               &:first-child {
                  li {
                     text-align: right;
                  }
               }

               li {
                  margin-bottom: 3rem;

                  @include media-breakpoint-down(sm) {
                     margin-bottom: 2rem;
                  }

                  .spec-value {
                     font-weight: 300;
                     font-size: 1.875rem;
                     line-height: 1.15;
                     color: theme-color(brand-yellow);
                  }

                  .spec-conversion {
                     font-size: 1.125rem;
                     color: $gray-400;
                  }

                  .spec-label {
                     color: white;
                     font-weight: $font-weight-bold;
                  }

                  @include media-breakpoint-down(md) {

                     .spec-value {
                        font-size: 1.5rem;
                     }

                     .spec-label {
                        font-size: 1rem;
                     }
                  }

                  @include media-breakpoint-down(sm) {
                     padding-right: 1rem;

                     .spec-value {
                        font-size: 1.35rem;
                     }

                     .spec-label {
                        font-size: 0.8rem;
                     }
                  }
               }
            }

            .drone-img {
               flex-basis: 45%;
               margin-top: 0rem;

               img+img {
                  margin-top: 4rem;
               }

               @include media-breakpoint-down(sm) {
                  margin-top: 0rem;
                  margin-bottom: 4rem;
                  flex-basis: 100%;
                  flex-grow: 1;
               }
            }
            .skytwo-drone {
               position: absolute;
               top:15%;
               left:13%;
               width: 1000px;
               z-index: 1;
               @include media-breakpoint-down(lg) {
                  width: 600px;
                  left:25%;
               }
               @include media-breakpoint-down(md) {
                  width: 550px;
                  left:20%;
                  top:20%;
               }
               @include media-breakpoint-down(sm) {
                  width: 300px;
                  top:-10%;
                  left:25%;
               }
            }
         }
      }

      &#ai-systems {
         background-color: rgba(theme-color(brand-green), 0.15);
         background-image:url('../img/brain-circuit.png');
         background-repeat: no-repeat;
         background-position: center right;
         padding-bottom: 5.5rem;

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
            background-size: 50%;
            background-position: top right;
         }

         .features {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;

            ul {
               list-style-type: none;
               margin: 0;
               padding: 0;
               flex-basis: 1;
               flex-basis: 25%;

               @include media-breakpoint-down(sm) {
                  flex-basis: 48%;
               }

               &:first-child {
                  li {
                     text-align: right;
                  }
               }

               li {
                  margin-bottom: 3rem;
                  font-weight: 300;
                  font-size: 1.875rem;
                  line-height: 1.15;
                  color: theme-color(brand-green);

                  @include media-breakpoint-down(md) {
                     font-size: 1.5rem
                  }

                  @include media-breakpoint-down(sm) {
                     margin-bottom: 2rem;
                     padding-right: 1rem;
                     font-size: 1.35rem;
                  }
               }
            }

            .ai-img {
               flex-basis: 45%;
               animation: bounce 3s ease-out 1s infinite;
               align-self: center;

               @include media-breakpoint-down(sm) {
                  margin-top: 0rem;
                  flex-basis: 100%;
                  flex-grow: 1;
               }
            }
         }

         .features-thumbs {

            ul {
               list-style-type: none;
               margin: 0;
               padding: 0;
               display: flex;
               flex-wrap: wrap;

               li {
                  width: 33.333%;
                  padding: 1rem;

                  span {
                     display: inline-block;
                     margin-top: 0.5rem;
                     color: darken(theme-color(brand-green), 20%);
                  }

                  @include media-breakpoint-down(md) {
                     width: 50%;
                  }

                  @include media-breakpoint-down(sm) {
                     width: 100%;
                  }
               }
            }
         }

         #aiSystemsCarousel {
            ol.carousel-indicators {
               margin:0;
               position: static;
               text-align: left;
               width: 100%;
               flex-direction: column;
               justify-content: flex-start;
               li {
                  box-sizing: border-box;
                  background-color: transparent;
                  -webkit-border-radius: 0;
                  border-radius: 0.15rem;
                  border:0;
                  border-bottom: 1px solid rgba(theme-color(brand-green),0.5);
                  display: flex;
                  height: auto;
                  margin: 0 !important;
                  width: 90%;
                  text-indent: 0;
                  color: darken(theme-color(brand-green),15%);
                  opacity: 1;
                  padding:1rem;
                  position: relative;
                  &:last-child {
                     border-bottom: 0;
                  }
                  &.active {
                     background-color: lighten(theme-color(brand-green), 30%);
                     color: $body-color;
                     align-items: center;
                     &:before {
                        border: 0;
                        position: absolute;
                        right: -1.5rem;
                        font-size: 2rem;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                        display: inline-block;
                        font-style: normal;
                        font-variant: normal;
                        font-weight: 400;
                        line-height: 1;
                        vertical-align: -.125em;
                        -webkit-transform: rotate(-270deg);
                        transform: rotate(-270deg);
                        font-family: "Font Awesome 5 Pro" !important;
                        font-weight: 600;
                        content: "\f2ec";
                        color: lighten(theme-color(brand-green), 30%);
                     }
                  }
                  h5 {
                     margin: 0;
                     font-weight: 300;
                     line-height: 1.15;
                  }
               }
               @include media-breakpoint-down(md) {
                  li {
                     h5 {
                        font-size: 1rem;
                     }
                  }
               }
               @include media-breakpoint-down(sm) {
                  padding-left: 15px;
                  margin-bottom: 2rem;
                  border:0!important;
                  li {
                     display: none;
                     width: auto;
                     text-align: center;
                     h5 {
                        text-align: center;
                        width: 100%;
                     }
                     &.active {
                        display:flex;
                        &:before {
                           top:0;
                           bottom:-1.5rem;
                           right:0;
                           left:0;
                           vertical-align: 0em;
                           -webkit-transform: rotate(-180deg);
                           transform: rotate(-180deg);
                        }
                     }
                  }
               }
            }
            a.carousel-control-prev,
            a.carousel-control-next {
               display: none;
               color:theme-color(brand-green);
               font-size: 1.875rem;
               top:25%;
               @include media-breakpoint-down(sm) {
                  display: flex;
               }
            }
         }
      }
   }
}