/* ==========================================================================
   Base
	========================================================================== */


/* General Layout
   ========================================================================== */
html,
body {
	height: 100%;
	overscroll-behavior: none;
}

#page {
	height: 100%;
	display: flex;
	flex-direction: column;
}

#content {
	align-self: stretch;
	flex: 1 0 auto;
}

/* Typography
   ========================================================================== */
h2.section-title,
h1.entry-title {
	padding-left: 5rem;
	padding-top: 5.5rem;
	margin-bottom: 6rem;
	font-weight: $font-weight-bold;
	position: relative;

	@include media-breakpoint-down(sm) {
		margin-bottom: 4rem;
	}

	&:before {
		content: ' ';
		position: absolute;
		top: 0;
		left: 4rem;
		width: 90px;
		height: 162px;
		transform: skew(-20deg);
		background-color: rgba(theme-color(brand-grey-light), 0.5);
		z-index: -2;
	}

	&:after {
		content: ' ';
		position: absolute;
		top: 0;
		left: 4.5rem;
		width: 4px;
		height: 121px;
		transform: skew(-20deg);
		background-color: theme-color(primary);
		z-index: -1;
	}

	@include media-breakpoint-down(md) {
		font-size: $h3-font-size;
		padding-top: 2.5rem;
		padding-left: 3.5rem;

		&:before {
			height: 100px;
			left: 2rem;
		}

		&:after {
			height: 65px;
			left: 2.4rem;
		}
	}

	@include media-breakpoint-down(sm) {
		font-size: $h4-font-size;
		padding-top: 2.5rem;
		padding-left: 3.5rem;

		&:before {
			height: 100px;
			left: 2rem;
		}

		&:after {
			height: 65px;
			left: 2.4rem;
		}
	}

	&.yellow {
		&:before {
			background-color: rgba(theme-color(brand-yellow), 0.15);
		}

		&:after {
			background-color: theme-color(brand-yellow);
		}
	}

	&.green {
		&:before {
			background-color: rgba(theme-color(brand-green), 0.15);
		}

		&:after {
			background-color: theme-color(brand-green);
		}
	}

	&.brown {
		&:before {
			background-color: rgba(theme-color(brand-brown), 0.15);
		}

		&:after {
			background-color: theme-color(brand-brown);
		}
	}
	&.red {
		&:before {
			background-color: rgba(theme-color(brand-red), 0.15);
		}

		&:after {
			background-color: theme-color(brand-red);
		}
	}

	&.grey {

		&:after {
			background-color: theme-color(brand-grey);
		}
	}
}

/* IE11 */
@media all and (-ms-high-contrast:none) {

	*::-ms-backdrop,
	[data-aos^=fade][data-aos^=fade],
	[data-aos^=zoom][data-aos^=zoom] {
		opacity: 1 !important;
		transform: translate(0px, 0px, 0px) !important;
	}
}