aside#secondary {
  margin-top:13rem;
  section {
    &.widget {
      margin-bottom:2rem;
      border:1px solid $input-border-color;
      padding:1.5rem;
      h2.widget-title {
        font-size:1.5rem;
        font-weight:$font-weight-bold;
      }
      ul {
        list-style-type:none;
        margin:0;
        padding:0;
        li {
          padding: 0.5rem 0;
          font-size:0.85rem;
        }
      }
      &.widget_search {
        .form-group {
          margin-bottom:0;
        }
      }
      &.widget_recent_entries {
        border:1px solid $input-border-color;
        padding:1.5rem;
      }
    }
  }
}