@import "../bootstrap/buttons";

.archive,
.search-results,
.search-no-results {
  .page-banner {
    height: 35rem;
  }
  #arch {
    padding-top:5rem;
    section#news {
      background-color: transparent;
      > ul {
        margin-bottom: 4rem;
      }
    }
    aside#secondary {
      margin-top: 0rem;
    }
  }

  .post-navigation {
    margin-top: 2rem;
    padding-left: 0rem!important;
    .nav-links {
      display: flex;
      justify-content: space-between;
      .nav-previous a,
      .nav-next a {
        @extend .btn;
        @extend .btn-outline-primary;
      }
    }
  }
}