section#call-to-action {
	background-color: rgba(theme-color(brand-grey-light), 0.3);
	background-image: url('../img/topographic-lines-bg.jpg');
	background-repeat: no-repeat;
	background-position: top center;
	background-size: cover;
	padding-top: 5.5rem;
	padding-bottom: 5.5rem;
	text-align: center;
	min-height: 392px;
	position: relative;
	overflow: hidden;

	@include media-breakpoint-down(sm) {
		min-height: 360px;
	}

	&:before {
		content: ' ';
		position: absolute;
		top: 0;
		right: 20%;
		width: 602px;
		height: 100%;
		transform: skew(-20deg);
		background-color: rgba(theme-color(primary), 0.1);
	}

	&:after {
		content: ' ';
		position: absolute;
		top: 0;
		left: 20%;
		width: 602px;
		height: 100%;
		transform: skew(20deg);
		background-color: rgba(white, 0.45);
	}

	@include media-breakpoint-down(md) {
		padding-top: 2.5rem;
		padding-bottom: 2.5rem;
	}

	.cta-content {
		position: absolute;
		left: 0;
		right: 0;
		z-index: 3;
		width: 100%;
	}

	h2 {
		font-size: 3.5rem;
		font-weight: $font-weight-bold;
		color: theme-color(primary);
		margin-bottom: 2rem;
	}

	p {
		font-size: 1.375rem;
		line-height: 1;

		// &.lead {
		// 	font-size: 1.625rem;
		// 	font-weight: $font-weight-bold;

    // }
    &:first-of-type {
      font-size: 1.625rem;
			font-weight: $font-weight-bold;
    }
	}

	.btn {
		margin-top: 1rem;
	}
}