/* IE11 */
@media all and (-ms-high-contrast:none) {
	.page-banner {
		background-repeat: no-repeat;
		background-position: top center;
		background-size: cover;
	}

	.home-banner {
		background-image: url('../img/homepage-banner-bg.jpg');
		background-repeat: no-repeat;
		background-position: top center;
		background-size: cover;
	}

	.solution .page-banner {
		background-image: url('../img/solution-banner-bg-v3.jpg');
	}

	.application .page-banner {
		background-image: url('../img/application-banner-bg.jpg');
	}

	.resources .page-banner {
		background-image: url('../img/resources-banner-bg.jpg');
	}

	.company .page-banner {
		background-image: url('../img/company-banner-bg.jpg');
	}

	.contact .page-banner {
		background-image: url('../img/contact-banner-bg.jpg');
	}

	.careers .page-banner {
		background-image: url('../img/careers-banner-bg.jpg');
	}

	#aerial-systems {
		background-image: url('../img/aerial-systems-bg.jpg');
		background-repeat: no-repeat;
		background-position: top center;
		background-size: cover;
	}

	#careers {
		background-image: url('../img/careers-bg.jpg');
		background-repeat: no-repeat;
		background-position: top center;
		background-size: cover;
	}

	.solution {
		section#droneAsAService {
			ul.services {
				li {
					.service-icon {
						margin-bottom: 1rem;

						&:before {
							top: 0rem;
							width: 90px;
							height: 140px;
						}

						@include media-breakpoint-between(md, lg) {
							margin-bottom: 1rem;

							&:before {
								height: 140px;
							}
						}
					}
				}
			}
		}
	}
}

// IE Edge 16+
@supports (-ms-ime-align:auto) {
	.page-banner {
		background-repeat: no-repeat;
		background-position: top center;
		background-attachment: fixed;
		background-size: cover;
	}

	.home-banner {
		background-image: url('../img/homepage-banner-bg.jpg');
		background-repeat: no-repeat;
		background-position: top center;
		background-attachment: fixed;
		background-size: cover;
	}

	.solution .page-banner {
		background-image: url('../img/solution-banner-bg-v3.jpg');
	}

	.application .page-banner {
		background-image: url('../img/application-banner-bg.jpg');
	}

	.resources .page-banner {
		background-image: url('../img/resources-banner-bg.jpg');
	}

	.company .page-banner {
		background-image: url('../img/company-banner-bg.jpg');
	}

	.contact .page-banner {
		background-image: url('../img/contact-banner-bg.jpg');
	}

	.careers .page-banner {
		background-image: url('../img/careers-banner-bg.jpg');
	}

	#aerial-systems {
		background-image: url('../img/aerial-systems-bg.jpg');
		background-repeat: no-repeat;
		background-position: top center;
		background-size: cover;
		background-attachment: fixed;
	}

	#careers {
		background-image: url('../img/careers-bg.jpg');
		background-repeat: no-repeat;
		background-position: top center;
		background-size: cover;
		background-attachment: fixed;
	}
}