.company {
  .page-banner {
    height: 35rem;
  }
   section {
      &#leadership-team {
         padding-bottom: 5.5rem;

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         ul.team {
            list-style-type: none;
            margin: 0;
            padding: 0;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;

            @include media-breakpoint-down(md) {
               justify-content: center;
            }

            li {
               flex-basis: 100%;
               padding: 0 1rem;
               margin-bottom: 3rem;
               display: flex;
               flex-direction: column;
               align-items: center;

               .member-photo {
                  margin-bottom: 2rem;
                  position: relative;
                  .img-top {
                     display: none;
                     position: absolute;
                     top: 0;
                     left: 0;
                     z-index: 99;
                  }
               }
               &:hover {
                  .member-photo .img-top {
                     display: inline;
                  }
               }

               .member-info {
                  display: flex;
                  flex-direction: column;
                  align-items: center;

                  .name {
                     font-weight: $font-weight-bold;
                  }

                  .linkedin {
                     padding-left: 0.5rem;
                  }
               }

               @include media-breakpoint-up(md) {
                  flex-basis: 25%;
               }

               @include media-breakpoint-down(md) {
                  flex-basis: 33.333%;
               }

               @include media-breakpoint-down(sm) {
                  flex-basis: 100%;
                  flex-direction: row;
                  margin-bottom: 2rem;

                  .member-photo {
                     width: 25%;
                     margin-bottom: 0;
                  }

                  .member-info {
                     display: flex;
                     flex-direction: column;
                     align-items: start;
                     padding-left: 1rem;

                     .name {}

                     .linkedin {
                        padding-left: 0.5rem;
                     }
                  }
               }

            }
         }
      }

      &#strategicPartnerships {
         padding-bottom: 5.5rem;

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         ul.partners {
            list-style-type: none;
            margin: 0;
            padding: 0;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-around;

            li {
               display: flex;
               align-items: center;
               padding-left: 1.5rem;
               padding-right: 1.5rem;
               margin-bottom: 2rem;

               a {
                  display: block;
                  max-height: 150px;
                  max-width: 175px;

                  img {
                     -webkit-filter: grayscale(100%);
                     filter: grayscale(100%);
                     opacity: 0.5;
                     transition: 0.5s all ease;
                  }

                  &:hover {
                     img {
                        -webkit-filter: none;
                        filter: none;
                        opacity: 1;
                     }
                  }
               }
            }
         }
      }
   }
}