section#upcoming-events {
	background-color: rgba($primary, 0.1);
	padding-bottom: 1rem;

	h2.section-title {
		padding-top: 2.5rem;
		padding-left: 0;
		margin-bottom: 2.5rem;
		text-align: center;
		color: $primary;

		&:before,
		&:after {
				display: none;
		}
	}

	ul {
		list-style-type: none;
		margin: 0;
		padding: 0;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		@include media-breakpoint-down(md) {
			justify-content: flex-start;
		}

		li {
			display: flex;
			width: 33.333%;
			margin-bottom: 2rem;
			flex-wrap: wrap;
			@include media-breakpoint-down(md) {
				width: 50%;
			}

			.card {
				width: 100%;
				margin:0 1rem;
				height: 100%;
				.card-body {
					display: flex;
					flex-direction: row;
					align-items: flex-start;
					height: 100%;
					@include media-breakpoint-down(sm) {
						flex-direction: column;
						align-items: flex-start;
						align-items: center;
						.event-details {
							margin-left:0!important;
							margin-top:0.5rem;
							text-align: center;
							.event-name {
								font-size: 1rem;
							}
							.event-time {
								font-size: 0.875rem;
							}
						}
					}
				}
				@include media-breakpoint-up(sm) {
					background-color: transparent;
					border:0;
					.card-body {
						padding:0;
					}
				}
			}
			.event-date {
				display: block;
				width: 100px;
				background: #fff;
				text-align: center;
				position: relative;
				flex-shrink: 0;
				margin-top: 0.75rem;
				.binds {
					position: absolute;
					height: 15px;
					width: 60px;
					background: transparent;
					border: 2px solid darken(theme-color(brand-grey), 20%);
					border-width: 0 5px;
					top: -6px;
					left: 0;
					right: 0;
					margin: auto;
				}
			
				.month {
					background:$primary;
					display: block;
					padding: 10px 0 8px 0;
					color: #fff;
					font-size: 12px;
					line-height: 1;
					font-weight: bold;
					box-shadow: inset 0 -1px 0 0 theme-color(brand-grey);
				}
			
				.day {
					display: block;
					margin: 0;
					padding: 10px 0;
					font-size: 48px;
					line-height: 1;
					box-shadow: 0 0 3px #ccc;
					color:theme-color(brand-grey);
					position: relative;
				}
			}
			.event-details {
				margin-left: 1rem;
				height: 100%;
				display: flex;
				flex-direction: column;
				.event-type {
					font-size: 0.75rem;
					letter-spacing: 1px;
					text-transform: uppercase;
					color: $primary;
				}
				.event-name {
					font-size: 1.15rem;
					line-height: 1.2;
					font-weight: 700;
					
				}
				.event-time {
					font-size: 1rem;
					@include media-breakpoint-up(md) {
						flex: 1 1 auto;
					}
				}
				.btn {
					flex-grow: 0;
					@include media-breakpoint-up(md) {
						align-self: flex-start;
					}
				}
			}

		}
	}
} 