.modal {
	.modal-dialog {
		.modal-content {
			.modal-header {
				padding: 2rem;
				background-color: theme-color(brand-yellow);

				@include media-breakpoint-down(md) {
					padding: 1rem 0.75rem;
					text-align: center;

					.modal-title {
						display: inline-block;
						width: 100%;
						padding-left: 2rem;
					}
				}
			}

			.modal-body {
				padding: 2rem;
				background-color: theme-color(brand-grey-light);

				@include media-breakpoint-down(md) {
					padding: 1rem 0.75rem;
				}
			}
		}
	}
}