// Bootsrap Variable Overrides
$primary: #4197CB;
$secondary: #A7A9AC;
$success: #28a745;
$info: #17a2b8;
$warning: #ffc107;
$danger: #dc3545;
$theme-colors: ();
$theme-colors: map-merge(("brand-yellow": #F6BD17,
            "brand-gold": #CCA054,
            "brand-green-muted":#6B7144,
            "brand-green":#9CA84E,
            "brand-brown": #8A5B45,
            "brand-red": #c23c32,
            "brand-grey": #54565B,
            "brand-grey-medium": #A7A9AC,
            "brand-grey-light":#EBEBEC), $theme-colors);
$font-family-sans-serif:'Oscine',
sans-serif;
