section#blog {

	ul {
		list-style-type: none;
		margin: 0;
		padding: 0;

		li {
			display: flex;
			width: 100%;
			flex-direction: row;
			margin-bottom: 2rem;

			@include media-breakpoint-down(md) {
				flex-direction: column;
			}

      .article-img {
        width: 25%;
        @include media-breakpoint-down(md) {
          width: 30%;
          margin-bottom: 1rem;
        }
        @include media-breakpoint-down(sm) {
          width: 100%;
        }
      }

			.story {
				font-size: 1rem;
        width: 75%;
        padding-left:2rem;
				@include media-breakpoint-down(md) {
          font-size: 0.875rem;
          padding-left:0rem;
          width: 100%;
        }
        h4.article-title {
          @include media-breakpoint-down(sm) {
            font-size: 1.25rem;
          }
        }
        .meta {
          color: $gray-600;
          font-size: 0.85rem;
        }

			}
		}
	}
} 