//Default Page Banner
.page-banner {
	height: 37rem;
	position: relative;
	text-align: center;
	overflow: hidden;

	/* IE11 */
	@media all and (-ms-high-contrast:none) {
		background-repeat: no-repeat;
		background-position: top center;
	}

	// IE Edge 16+
	@supports (-ms-ime-align:auto) {
		background-repeat: no-repeat;
		background-position: top center;
		background-attachment: fixed;
	}

	@include media-breakpoint-down(md) {
		height: 30rem;
	}

	@include media-breakpoint-down(sm) {
		height: 100vh;
	}

	&:after {
		position: absolute;
		content: '';
		height: 200px;
		width: 100%;
		top: 0;
		left: 0;
		right: 0;
		z-index: 3;
		@include gradient-y($start-color: rgba(black, 0.35), $end-color: transparent, $start-percent: 0%, $end-percent: 50%);
	}

	h1.page-title {
		display: inline-block;
		font-size: 6.25rem;
		font-weight: $font-weight-bold;
		line-height: 1;
		color: white;
		position: relative;
		padding-bottom: 3rem;
		margin-top: 3rem;

		+p {
			margin-top: 2rem;
		}

		+.btn {
			margin-top: 2rem;
		}

		&:before {
			content: ' ';
			position: absolute;
			top: -20px;
			left: -30px;
			width: 2px;
			height: 100%;
			transform: skew(-20deg);
			background-color: white;
			z-index: 1;
		}

		&:after {
			content: ' ';
			position: absolute;
			top: -20px;
			right: -30px;
			width: 2px;
			height: 100%;
			transform: skew(-20deg);
			background-color: white;
			z-index: 1;
		}

		@include media-breakpoint-down(lg) {
			font-size: 4rem;
			margin-top: 2rem;
			padding-bottom: 2rem;

		}

		@include media-breakpoint-down(md) {
			font-size: 3rem;
		}

		@include media-breakpoint-down(sm) {

			&:before {
				top: -15px;
				left: -15px;
				// height: 100%px;
			}

			&:after {
				top: -15px;
				right: -15px;
				// height: 80px;
			}
		}

		// &:before {
		// 	content: ' ';
		// 	position: absolute;
		// 	top: -0.75rem;
		// 	left: 4rem;
		// 	width: 90px;
		// 	height: 162px;
		// 	transform: skew(-20deg);
		// 	background-color: rgba(255, 255, 255, 0.25);
		// 	z-index: -2;
		// }

		// @include media-breakpoint-down(md) {
		// 	font-size: 3rem;
		// 	margin-bottom: 2rem;

		// 	&:before {
		// 		top: -15px;
		// 		left: 2rem;
		// 		height: 80px;
		// 		width: 60px;
		// 	}
		// }
	}

	p {
		font-size: 1.25rem;
		color: white;

		@include media-breakpoint-down(md) {
			font-size: 1.125rem;
		}
	}

	.anchor {
		width: 100%;
		height: 44px;
		position: absolute;
		right: 0;
		left: 0;
		bottom: 10%;
		display: none;
		text-align: center;

		@include media-breakpoint-down(sm) {
			display: inline-block;
		}

		.scroll-down {
			margin-left: auto;
			margin-right: auto;
		}
	}
}

//Home Page Banner
.home-banner {
	height: 60vh;
	// height: 100vh; for video
	text-align: center;
	position: relative;
	overflow: hidden;

	#homepageVideo {
		position: absolute;
		top: 50%;
		left: 50%;
		width: auto;
		min-width: 100%;
		height: auto;
		min-height: 100%;
		transform: translateX(-50%) translateY(-50%);
		z-index: -1;
		@include media-breakpoint-down(sm) {
			height: inherit;
		}
	
	 }
	.banner-overlay {
		position: absolute;
		background-color: rgba(0,0,0,0.25);
		right: 0;
		left:0;
		top:0;
		bottom: 0;
		z-index: 1;
	}

	@include media-breakpoint-down(md) {
		height: 30rem;
	}

	@include media-breakpoint-down(sm) {
		height: 20rem;
	}

	h1.page-title {
		display: block;
		font-size: 6.25rem;
		font-weight: $font-weight-bold;
		color: theme-color(brand-yellow);
		position: relative;
		z-index: 2;
		&:before {
			content: ' ';
			position: absolute;
			top: -20px;
			left: -30px;
			width: 2px;
			height: 150px;
			transform: skew(-20deg);
			background-color: theme-color(brand-yellow);
			z-index: 2;
		}

		&:after {
			content: ' ';
			position: absolute;
			top: -20px;
			right: -30px;
			width: 2px;
			height: 150px;
			transform: skew(-20deg);
			background-color: theme-color(brand-yellow);
			z-index: 2;
		}

		@include media-breakpoint-down(sm) {
			font-size: 3rem;

			&:before {
				top: -15px;
				left: -15px;
				height: 80px;
			}

			&:after {
				top: -15px;
				right: -15px;
				height: 80px;
			}
		}
	}

	.anchor {
		width: 44px;
		height: 44px;
		position: absolute;
		bottom: 20%;
		display: block;
		z-index: 3;
		/* IE11 */
		@media all and (-ms-high-contrast:none) {
			left: 45%;
		}

		@include media-breakpoint-down(md) {
			display: none;
		}
	}
}


