.application {
  .page-banner {
    height: 35rem;
  }
   section {
      &#applications {
         padding-top: 1.5rem;
         padding-bottom: 1.5rem;

         @include media-breakpoint-down(md) {
            padding-top: 1.5rem;
            padding-bottom: 2.5rem;
         }

         ul#applicationNav {
            width: 100%;

            &.nav-pills {
               li.nav-item {
                  .nav-link {
                     display: flex;
                     flex-direction: column;
                     align-items: center;
                     font-size: 1.25rem;
                     color: theme-color(brand-grey-medium);

                     .application-icon {
                        width: 155px;
                        margin-bottom: 2rem;
                        position: relative;
                        @include media-breakpoint-down(md) {
                          width: 125px;
                        }

                        svg {
                           #external-third-party-disruption-detection-icon {
                              .cls-1 {
                                 stroke: theme-color(brand-grey-medium);
                              }
                           }
                           #illegal-activity-icon{
                              .cls-1,.cls-2 {
                                 stroke: theme-color(brand-grey-medium);
                              }
                           }
                           #right-of-way-icon {
                              .cls-1 {
                                 stroke: theme-color(brand-grey-medium);
                              }
                           }

                           #leak-verification-icon {

                              .cls-1,
                              .cls-2,
                              .cls-3 {
                                 stroke: theme-color(brand-grey-medium);
                              }
                           }

                           #corrosion-monitoring-icon {
                              .cls-1 {
                                 stroke: theme-color(brand-grey-medium);
                              }
                           }
                        }
                     }

                     &:hover {
                        .application-icon {
                           &:before {
                              content: ' ';
                              position: absolute;
                              top: -2rem;
                              left: 25%;
                              width: 90px;
                              height: 145px;
                              transform: skew(-20deg);
                              background-color: rgba(theme-color(brand-grey-light), 0.5);
                              z-index: -2;
                           }
                        }
                     }

                     &.active {
                        background-color: transparent;
                        color: theme-color(brand-gold);

                        .application-icon {
                           &:before {
                              content: ' ';
                              position: absolute;
                              top: -2rem;
                              left: 25%;
                              width: 90px;
                              height: 145px;
                              transform: skew(-20deg);
                              background-color: rgba(theme-color(brand-yellow), 0.1);
                              z-index: -2;
                           }

                           svg {
                              #external-third-party-disruption-detection-icon {
                                 .cls-1 {
                                    stroke: darken(theme-color(brand-gold), 10%);
                                 }
                              }
                              #illegal-activity-icon{
                                 .cls-1,.cls-2 {
                                    stroke: darken(theme-color(brand-gold), 10%);
                                 }
                              }
                              #right-of-way-icon {
                                 .cls-1 {
                                    stroke: theme-color(brand-gold);
                                 }
                              }

                              #leak-verification-icon {

                                 .cls-1,
                                 .cls-2,
                                 .cls-3 {
                                    stroke: theme-color(brand-gold);
                                 }
                              }

                              #corrosion-monitoring-icon {
                                 .cls-1 {
                                    stroke: theme-color(brand-gold);
                                 }
                              }
                           }
                        }
                     }
                  }

                  @include media-breakpoint-down(sm) {
                     .nav-link {
                        font-size: 0.875rem;
                        line-height: 1.15;
                        padding: .5rem 0.5rem;

                        .application-icon {
                           width: 50px;
                        }

                        &:hover {
                           .application-icon {
                              &:before {
                                 content: ' ';
                                 position: absolute;
                                 top: -2rem;
                                 left: 25%;
                                 width: 60px;
                                 height: 100px;
                                 transform: skew(-20deg);
                                 background-color: rgba(theme-color(brand-grey-light), 0.5);
                                 z-index: -2;
                              }
                           }
                        }

                        &.active {
                           .application-icon {
                              &:before {
                                 content: ' ';
                                 position: absolute;
                                 top: -2rem;
                                 left: 25%;
                                 width: 60px;
                                 height: 100px;
                                 transform: skew(-20deg);
                                 background-color: rgba(theme-color(brand-yellow), 0.1);
                                 z-index: -2;
                              }
                           }
                        }
                     }
                  }
               }
            }
         }
      }
   }

   #applicationContent {
      background-color: rgba(theme-color(brand-yellow), 0.1);
      h3 {
         color: theme-color(brand-gold);
         margin-bottom: 2rem;
         &.approach-title {
            margin-top:5rem;
         }
      }

      ul.steps {
         list-style-type: none;
         margin: 0;
         padding: 0;

         >li {
            display: flex;
            align-items: baseline;
            margin-bottom: 2rem;

            @include media-breakpoint-down(md) {
               flex-direction: column;
            }

            .step-count {
               width:150px;
               font-size: 2rem;
               padding-left: 1rem;
               font-weight: $font-weight-bold;
               position: relative;
               // color: theme-color(brand-gold);
               &:after {
                  content: ' ';
                  position: absolute;
                  top: 0;
                  left:0;
                  width: 4px;
                  height: 40px;
                  transform: skew(-20deg);
                  background-color: theme-color(brand-gold);
                  z-index: -1;
               }
            }

            .step-description {
               flex-basis: 80%;
               padding-right: 2rem;
               font-size: 1rem;
               @include media-breakpoint-down(md) {
                  padding-right: 0rem;
               }

               ul {
                  list-style-type: disc !important;
                  margin: 0.5rem 0 0 1rem;
                  padding: 0;
               }
            }
         }
      }

      h3.benefit-title {
         color: theme-color(brand-gold)!important;
         margin-top:5rem;
      }

      ul.benefits {
         list-style-type: none;
         margin: 0;
         padding: 0;
         justify-content: flex-start;
         @include media-breakpoint-down(md) {
            justify-content: space-evenly;
         }


         @include media-breakpoint-down(sm) {
            flex-direction: column;
         }

         li {
            // border: 1px solid theme-color(brand-green);
            // background-color:rgba(theme-color(brand-green),0.1);
            // color: theme-color(brand-green);
            // border-radius: 0.3rem;
            // text-align: center;
            @include media-breakpoint-down(md) {
               // width: 47%;
               margin-bottom: 2rem;
            }
            @include media-breakpoint-down(sm) {
               // width: 100%;
               margin-bottom: 2rem;
            }

            .benefit-label {
               // border: 1px solid theme-color(brand-gold);
               background-color:rgba(theme-color(brand-gold),0.1);
               color: darken(theme-color(brand-gold),5%);
               border-radius: 0.3rem;
               text-align: center;
               font-size: 1.15rem;
               line-height: 1.15;
               display: flex;
               flex-direction: column;
               align-items: center;
               height: 100%;
               padding:1rem 1rem 2rem 1rem;
               .fa-check {
                  font-size: 1.725rem;
                  margin-bottom: 1rem;
                  width: 60px;
                  height: 60px;
                  border-radius: 30px;
                  background-color: white;
                  display:flex;
                  align-items: center;
                  justify-content: center;
                  // border: 1px solid theme-color(brand-gold);
               }
               @include media-breakpoint-down(md) {
                  font-size: 1rem;
               }
               @include media-breakpoint-down(sm) {
                  font-size: 1.25rem;
               }
            }

            .benefit-description {
               padding-left: 2rem;
            }
         }
      }

      section.results {
         background-color:rgba(darken(theme-color(brand-gold),40%),0.5);
         background-size:cover;
         background-repeat: no-repeat;
         background-position: center center;
         padding:7rem 0 5rem 0;
         margin-top:5rem;
      }

      h3.results-title {
         color: theme-color(brand-yellow);
      }
      
      ul.results {
         list-style-type: none;
         margin: 0;
         padding: 0;
         display: flex;
         flex-direction: row;
         justify-content: space-between;

         @include media-breakpoint-down(sm) {
            flex-direction: column;
         }

         li {
            width: 32%;
            padding: 0rem 2rem 2rem 1rem;
            // border: 1px solid theme-color(brand-gold);
            color: white;
            border-radius: 0.3rem;

            @include media-breakpoint-down(sm) {
               width: 100%;
               margin-bottom: 2rem;
            }

            .result-label {
               font-size: 1.25rem;
               line-height: 1.15;
               font-weight: $font-weight-bold;
               margin-bottom: 1rem;
               display: flex;
               color: theme-color(brand-yellow);

               .fa-check {
                  padding-right: 1rem;
               }
            }
         }
		}
		
		#leak-verification {
			position: relative;
			&:after {
				content: "";
				background-image:url('../img/leak_verification_main.png');
				background-position: top right;
				background-repeat: no-repeat;
				opacity: 0.35;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				position: absolute;
				z-index: -1;
				@include media-breakpoint-down(md) {
					opacity: 0.15;
			 }
				@include media-breakpoint-down(sm) {
					opacity: 0;
			 }
			}
		}
		#corrosion-monitoring{
			position: relative;
			&:after {
				content: "";
				background-image:url('../img/corrosion_monitoring_main.png');
				background-position: top right;
				background-repeat: no-repeat;
				opacity: 0.35;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				position: absolute;
				z-index: -1;
				@include media-breakpoint-down(md) {
					opacity: 0.15;
			 }
				@include media-breakpoint-down(sm) {
					opacity: 0;
			 }
			}
		}
		#right-of-way{
			position: relative;
			&:after {
				content: "";
				background-image:url('../img/right_of_way_main.png');
				background-position: top right;
				background-repeat: no-repeat;
				opacity: 0.5;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				position: absolute;
				z-index: -1;
				@include media-breakpoint-down(md) {
					opacity: 0.15;
			 }
				@include media-breakpoint-down(sm) {
					opacity: 0;
			 }
			}
		}
		#third-party-external-disruption {
			position: relative;
			&:after {
				content: "";
				background-image:url('../img/illegal_activity_main.png');
				background-position: top right;
				background-repeat: no-repeat;
				opacity: 0.35;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				position: absolute;
				z-index: -1;
				@include media-breakpoint-down(md) {
					opacity: 0.15;
			 }
				@include media-breakpoint-down(sm) {
					opacity: 0;
			 }
			}
		}
   }
}