/* ==========================================================================
   Utilities
	========================================================================== */


/* Fading
   ========================================================================== */
.fade-in {
	visibility: visible;
	opacity: 1;
	transition: opacity 1s linear;
}

.fade-out {
	visibility: hidden;
	opacity: 0;
	transition: visibility 0s 1s, opacity 1s linear;
}

/* Parallax Fix
   ========================================================================== */
.parallax-window {
	min-height: 400px;
	background: transparent;
}


/* Scroll Down Icon
   ========================================================================== */
.scroll-down {
	position: relative;
	width: 24px;
	height: 24px;
}

.chevron {
	position: absolute;
	width: 28px;
	height: 2px;
	opacity: 0;
	transform: scale3d(0.5, 0.5, 0.5);
	animation: move 3s ease-out infinite;
}

.chevron:first-child {
	animation: move 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
	animation: move 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
	content: ' ';
	position: absolute;
	top: 0;
	height: 100%;
	width: 51%;
	background: #fff;
}

.chevron:before {
	left: 0;
	transform: skew(0deg, 30deg);
}

.chevron:after {
	right: 0;
	width: 50%;
	transform: skew(0deg, -30deg);
}

@keyframes move {
	25% {
		opacity: 1;

	}

	33% {
		opacity: 1;
		transform: translateY(30px);
	}

	67% {
		opacity: 1;
		transform: translateY(40px);
	}

	100% {
		opacity: 0;
		transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
	}
}




/* AOS Vendor Annimation
	========================================================================== */
// https://github.com/michalsnik/aos
@import url("https://unpkg.com/aos@next/dist/aos.css");


/* Remove Branding from Google Maps
	========================================================================== */
a[href^="http://maps.google.com/maps"] {
	display: none !important
}

a[href^="https://maps.google.com/maps"] {
	display: none !important
}

.gmnoprint a,
.gmnoprint span,
.gm-style-cc {
	display: none;
}

.gmnoprint div {
	background: none !important;
}

/* Embedded Video Container
	========================================================================== */
.video-container {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;

	iframe,
	object,
	embed {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}