.solution {
  .page-banner {
    height: 35rem;
  }
   section {
      &#howItWorks {
         padding-bottom: 5.5rem;

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         .plyr--video {
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19),
               0 6px 6px rgba(0, 0, 0, 0.23);
         }
      }

      &#howWereBetter {
         padding-bottom: 5.5rem;
         background-color: rgba(theme-color(brand-green), 0.1);

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         table {

            &.table-striped>tbody>tr:nth-child(2n+1)>td,
            &.table-striped>tbody>tr:nth-child(2n+1)>th {
               background-color: rgba(theme-color(brand-green), 0.1);
            }

            &.table-striped>tbody>tr:nth-child(2n+1)>td.skyx,
            &.table-striped>tbody>tr:nth-child(2n+1)>th.skyx {
               background-color: rgba(theme-color(brand-yellow), 1);
            }

            thead {
               tr {
                  &:first-child th:nth-child(3) {
                     text-transform: uppercase;
                     font-size: 0.875rem;
                     letter-spacing: 2px;
                  }

                  th {
                     text-align: center;
                     border: 0;
                     width: 100px;
                     font-size: 1.15rem;
                     line-height: 1;

                     &:first-child {
                        border-top: 0;
                        border-left: 0;
                     }

                     &.skyx {
                        padding: 2rem 2rem 1rem 2rem;
                        font-size: 1.25rem;
                        background-color: rgba(theme-color(brand-yellow), 0.9);
                        font-weight: $font-weight-bold;
                        // border-right: 2px solid theme-color(brand-yellow);
                        // border-left: 2px solid theme-color(brand-yellow);
                        // border-top: 2px solid theme-color(brand-yellow);

                        img {
                           width: 80px;
                           margin-bottom: 1rem;
                        }
                     }

                     &.competition {
                        vertical-align: bottom !important;
                     }
                  }
               }
            }

            tbody {
               tr {
                  th {
                     font-size: 0.85rem;
                     font-weight: normal;
                     border: 0;
                     text-align: right;
                     color: darken(theme-color(brand-grey), 15%);
                     vertical-align: middle;
                  }

                  td {
                     text-align: center;
                     line-height: 1;
                     color: theme-color(brand-grey);
                     vertical-align: middle;
                     border: 0;

                     .fa-check {
                        font-size: 1.5rem;
                        line-height: 1;
                        color: darken(theme-color(brand-green),15%);
                     }

                     .fa-minus {
                        color: $gray-500;
                     }
                     .fa-infinity,
                     .fa-border-all,
                     .fa-border-inner,
                     .fa-border-none {
                        font-size: 1.35rem;
                        color: darken(theme-color(brand-green),15%);
                     }

                     &.skyx {
                        background-color: rgba(theme-color(brand-yellow), 0.9);
                        font-weight: $font-weight-bold;
                        color: $body-color;

                        // border-right: 2px solid theme-color(brand-yellow);
                        // border-left: 2px solid theme-color(brand-yellow);
                        .fa-check,
                        .fa-infinity,
                        .fa-border-all,
                        .fa-border-inner,
                        .fa-border-none {
                           color: $body-color;
                        }
                        
                        .front-icons {
                           color: $body-color;
                        }
                        .back-icons {
                           display: flex;
                           color: darken(theme-color(brand-yellow), 10%);
                           position: relative;
                        }
                     }

                     &:last-child {
                        border-right: 0;
                     }
                  }

                  &:last-child td.skyx {
                     // border-bottom: 2px solid theme-color(brand-yellow) !important;
                  }
               }
            }
         }
      }

      &#droneAsAService {
         padding-bottom: 5.5rem;

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         ul.services {
            list-style-type: none;
            margin: 0;
            padding: 0;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;

            li {
               display: flex;
               flex-direction: column;
               align-items: center;
               width: 25%;
               padding: 0 1rem;
               margin-top: 4rem;

               @include media-breakpoint-down(sm) {
                  margin-top: 2rem;
                  margin-top: 2rem;
               }

               .service-icon {
                  width: 100px;
                  height: 143px;
                  margin-bottom: 1rem;
                  position: relative;

                  &:before {
                     content: ' ';
                     position: absolute;
                     top: -2rem;
                     left: 35%;
                     width: 90px;
                     height: 180px;
                     transform: skew(-20deg);
                     background-color: rgba(theme-color(brand-green), 0.1);
                     z-index: -2;
                  }

                  @include media-breakpoint-between(md, lg) {
                     margin-bottom: 0rem;

                     &:before {
                        height: 160px;
                     }
                  }
               }

               .service-description {
                  font-size: 1.25rem;
                  text-align: center;
                  width: 100%;

                  @include media-breakpoint-down(md) {
                     font-size: 1rem;
                  }
               }

               @include media-breakpoint-down(md) {
                  width: 32%;
               }

               @include media-breakpoint-down(sm) {
                  flex-direction: row;
                  width: 100%;
                  align-items: center;

                  .service-icon {
                     margin-bottom: 0rem;
                     height: auto;
                     width: 25%;

                     &:before {
                        top: -0.5rem;
                        left: 20%;
                        width: 50px;
                        height: 100px;
                     }
                  }

                  .service-description {
                     text-align: left;
                     width: 75%;
                     padding-left: 2rem;
                  }
               }
            }
         }
      }
   }
}

.star-rating {
   display: flex;
   align-items: center;
   font-size: 1.35rem;
   justify-content: center;
}
.back-icons {
   display: flex;
   color: lighten(theme-color(brand-green),10%);
   position: relative;
   .fa-dollar-sign,
   .fa-grip-lines  {
      padding:0 0.15rem;
   }
}
.front-icons {
   display: flex;
   color: darken(theme-color(brand-green),15%);
   overflow: hidden;
   position: absolute;
   top: 0;
   .fa-dollar-sign,
   .fa-grip-lines {
      padding:0 0.15rem;
   }
}