.careers {
  .page-banner {
    height: 35rem;
  }
   section {
      &#team {
         padding-bottom: 1.5rem;
         background-color: theme-color(brand-yellow);

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         h2.section-title {
            padding-top: 3rem;
            padding-left: 0;
            text-align: center;
            margin-bottom: 2rem;

            &:before,
            &:after {
               display: none;
            }
         }

         img {
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19),
               0 6px 6px rgba(0, 0, 0, 0.23);
         }
      }

      &#teamPhoto {
         min-height: 500px;
         background-color: rgba(theme-color(brand-yellow), 0.1);
      }

      &#perksBenefits {
         padding-bottom: 3.5rem;
         background-color: rgba(theme-color(brand-yellow), 0.1);

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         ul.perks {
            list-style-type: none;
            margin: 0;
            padding: 0;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;

            li {
               display: flex;
               flex-direction: column;
               align-items: center;
               flex-basis: 25%;
               padding: 0 1rem;
               margin-bottom: 4rem;

               .service-icon {
                  width: 100px;
                  height: 143px;
                  margin-bottom: 1rem;
                  position: relative;

                  &:before {
                     content: ' ';
                     position: absolute;
                     top: -2rem;
                     left: 35%;
                     width: 90px;
                     height: 180px;
                     transform: skew(-20deg);
                     background-color: rgba(theme-color(brand-yellow), 0.1);
                     z-index: -2;
                  }

                  @include media-breakpoint-between(md, lg) {
                     margin-bottom: 0rem;

                     &:before {
                        height: 160px;
                     }
                  }
               }

               .service-description {
                  font-size: 1.25rem;
                  text-align: center;
               }
            }
         }
      }

      &#testimonials {
         padding-bottom: 5.5rem;

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         ul.quotes {
            list-style-type: none;
            margin: 0;
            padding: 0;

            li {
               display: flex;
               align-items: center;
               margin-bottom: 4rem;

               @include media-breakpoint-down(sm) {
                  margin-bottom: 2em;
               }

               .user-photo {
                  flex-basis: 20%;
                  padding: 0 1rem;

                  img {
                     max-width: 140px;
                     border: 4px solid theme-color(brand-yellow);
                     border-radius: 100%;
                  }

                  @include media-breakpoint-down(sm) {
                     display: none;
                  }
               }

               .user-quote {
                  flex-basis: 80%;

                  blockquote {
                     margin: 0;
                  }

                  @include media-breakpoint-down(sm) {
                     flex-basis: 100%;

                     blockquote {
                        font-size: 1rem;
                     }
                  }
               }

               &:nth-child(even) {
                  text-align: right;

                  .user-photo {
                     order: 1;
                  }

                  .user-quote {
                     order: 0;

                  }

                  @include media-breakpoint-down(sm) {
                     text-align: left;

                     .user-photo {
                        order: 0;
                     }

                     .user-quote {
                        order: 1;
                     }
                  }
               }
            }
         }
      }

      &#byTheNumbers {
         padding-bottom: 5.5rem;

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }
      }

      &#openPositions {
         padding-bottom: 5.5rem;
         background-color: rgba(theme-color(brand-yellow), 0.1);

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         ul.job-list {
            list-style-type: none;
            margin: 0;
            padding: 0;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            li {
               flex-basis: 30%;
               font-size: 1.15rem;
               margin-bottom: 2rem;

               @include media-breakpoint-down(md) {
                  flex-basis: 50%;
               }

               @include media-breakpoint-down(sm) {
                  flex-basis: 100%;
               }

               .job-title {
                  color: theme-color(brand-gold);
                  position: relative;
                  margin-left: 2rem;
                  display: block;
                  line-height: 1.15;

                  &:before {
                     font-family: 'Font Awesome 5 Pro';
                     font-size: 1rem;
                     content: '\f178';
                     color: theme-color(brand-gold);
                     position: absolute;
                     left: -2rem;
                  }
               }

               .job-location {
                  font-size: 0.875rem;
                  margin-left: 2rem;
               }


            }
         }
      }
   }
}

// Bamboo Script Overrides 
.BambooHR-ATS-board {
   h2 {
      display: none;
   }
   ul.BambooHR-ATS-Department-List {
      display: flex;
      flex-wrap: wrap;
      overflow-x: hidden;
      > li {
         flex-basis: 33.333%!important;
         padding-right: 2rem;
         @include media-breakpoint-down(md) {
            flex-basis: 50%!important;
         }

         @include media-breakpoint-down(sm) {
            flex-basis: 100%!important;
         }
         .BambooHR-ATS-Department-Header {
            margin-left: 2rem;
            margin-bottom: 1rem;
            font-size: 0.875rem;
            text-transform: uppercase;
            letter-spacing: 1px;
            position: relative;
         }
      }
   }
   ul.BambooHR-ATS-Jobs-List {
      list-style-type: none;
      margin: 0 0 1.5rem 0;
      padding: 0;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      justify-content: flex-start;
      border-bottom: 0;

      li {
         flex-basis: 100%!important;
         font-size: 1.15rem!important;
         margin-bottom: 1.5rem!important;

         a {
            color: theme-color(brand-gold);
            position: relative;
            margin-left: 2rem;
            display: block;
            line-height: 1.15;
            font-weight: normal;

            &:before {
               font-family: 'Font Awesome 5 Pro';
               font-size: 1rem;
               content: '\f178';
               color: theme-color(brand-gold);
               position: absolute;
               left: -2rem;
            }
         }

         .BambooHR-ATS-Location {
            font-size: 0.875rem;
            margin-left: 2rem;
            float:none!important;
            color:$body-color;
         }
      }
   }
}

#BambooHR {
   #BambooHR-Footer {
      text-align: left;
      margin-left: 2rem;
      display: none;
   }
}