.contact {
   background-color: theme-color(brand-grey);
   .page-banner {
      height: 35rem;
      // text-align:center;
   }
   section {
      &#contactForm {
         padding-bottom: 2.5rem;
         text-align: center;

         h2.section-title {
            padding-top: 2.5rem;
            padding-left: 0;
            text-align: center;
            color: white;
            margin-bottom: 2rem;
         
            

            &:before,
            &:after {
               display: none;
            }

            .btn {
               margin-left: 1rem;
            }
         }

         #cf7md-form .wpcf7-not-valid-tip {
            background-color: transparent;
            text-align: left;
            color: theme-color(brand-yellow); 
         }
         

      }
      &#officeLocations {
         padding-bottom: 0rem;
         text-align: center;

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         h2.section-title {
            padding-top: 2rem;
            padding-left: 0;
            text-align: center;
            color: theme-color(brand-grey-medium);
            margin-bottom: 2rem;

            &:before,
            &:after {
               display: none;
            }
         }

         ul.locations {
            list-style-type: none;
            margin:0;
            padding:0;
            display: flex;
            @include media-breakpoint-down(sm) {
               flex-direction: column;
            }
            li {
               flex-basis: 33.333%;
               border-right:2px solid theme-color(brand-grey);
               &:last-child {
                  border-right:0;
               }
               @include media-breakpoint-down(sm) {
                  flex-basis: 100%;
                  margin-top: 2rem;
                  &:first-child {
                     margin-top: 0;
                  }
                  border-right: 0;
               }
               .location-info {
                  .location-type {
                     font-size: 1.5rem;
                     font-weight: $font-weight-bold;
                     color: theme-color(brand-yellow);
                     line-height: 0.9;
                  }
                  .location-place {
                     font-size: 1.5rem;
                     font-weight: $font-weight-bold;
                     color: theme-color(brand-grey-medium);
                  }
                  .location-map {
                     margin-top: 0.5rem;
                     width: 100%;
                     height: 370px;
                     background-color: black;
                     @include media-breakpoint-down(sm) {
                        height: 200px;
                     }
                  }
               }
            }
         }
      }
   }
}