@import "../bootstrap/buttons";

.single {
   section {
      &#story {
         padding-bottom: 5.5rem;

         @include media-breakpoint-down(md) {
            padding-bottom: 2.5rem;
         }

         article {
          h1.entry-title {
            margin-bottom:2rem;
          }
          .entry-meta {
            margin-bottom: 2rem;
            padding-left: 5rem;
            @include media-breakpoint-down(md) {
              padding-left: 3.5rem;
            }
          
            @include media-breakpoint-down(sm) {
              padding-left: 3.5rem;
            }
            span.posted-on {
              color: $gray-600;
					    font-size: 0.875rem;
            }
          }
          .entry-content {
            padding-left: 5rem;
            @include media-breakpoint-down(md) {
              padding-left: 3.5rem;
            }
          
            @include media-breakpoint-down(sm) {
              padding-left: 0rem;
            }
            img {
              max-width: 100%;
              height: auto;
            }
          }
        }
      }
   }

  .post-navigation {
    margin-top: 2rem;
    padding-left: 5rem;
    @include media-breakpoint-down(md) {
      padding-left: 3.5rem;
    }
  
    @include media-breakpoint-down(sm) {
      padding-left: 0rem;
    }
    .nav-links {
      display: flex;
      justify-content: space-between;
      .nav-previous a,
      .nav-next a {
        @extend .btn;
        @extend .btn-outline-primary;
      }
    }
  }
  .disqus_thread {
    padding-left: 5rem;
    @include media-breakpoint-down(md) {
      padding-left: 3.5rem;
    }
    @include media-breakpoint-down(sm) {
      padding-left: 0rem;
    }
  }
}