section#news {
	background-color: rgba(theme-color(brand-grey-light), 0.3);
	padding-bottom: 5.5rem;

	@include media-breakpoint-down(md) {
		padding-bottom: 2.5rem;
	}

	h5 {
		display:flex;
		align-items: center;
		.fa-newspaper,
		.fa-microphone-alt {
			font-size: 1.875rem;
			color: theme-color(brand-grey);
			@include media-breakpoint-down(sm) {
				font-size: 1.35rem;
			} 
		}
	}

	ul {
		list-style-type: none;
		margin: 0;
		padding: 0;
		@include media-breakpoint-down(sm) {
			&.news {
				margin-bottom: 4rem;
			}
		}

		li {
			display: flex;
			width: 100%;
			flex-direction: row;
			margin-bottom: 2rem;
			align-items: baseline;

			@include media-breakpoint-down(sm) {
				flex-direction: column;
				align-items: flex-start;
			}

			.meta {
				width: 25%;
				display: flex;
				align-items: baseline;

				@include media-breakpoint-down(sm) {
					padding-left: 2.8rem;
					width: 100%;
				}

				i {
					padding-right: 1rem;
					font-size: 1.875rem;
					color: theme-color(brand-grey);

					@include media-breakpoint-down(sm) {
						font-size: 1.35rem;
					}
				}

				.date {
					
					color: $gray-600;
					font-size: 0.875rem;
				}
			}

			.story {
				display: flex;
				width: 75%;
				align-items: baseline;
				font-size: 1.125rem;

				@include media-breakpoint-down(md) {
					font-size: 1rem;
				}

				@include media-breakpoint-down(sm) {
					padding-left: 1rem;
					width: 100%;
				}

				i {
					padding-right: 1rem;
					color: theme-color(primary);
				}

				span.is-pr {
					font-size: 0.75rem;
					color: $gray-600;
					padding-right: 0.25rem;
				}

				a.article-title {
					color: $body-color;
					text-decoration: none;

					&:hover {
						color: theme-color(primary);
					}


				}
			}
		}
	}
} 