section#careers {
	padding-top: 5rem;
	padding-bottom: 5.5rem;
	text-align: center;
	min-height: 400px;
	position: relative;
	overflow: hidden;

	@include media-breakpoint-down(sm) {
		min-height: 360px;
	}

	@include media-breakpoint-down(md) {
		padding-top: 2.5rem;
		padding-bottom: 2.5rem;
	}

	.cta-content {
		position: absolute;
		left: 0;
		right: 0;
		z-index: 3;
		width: 100%;
	}

	h2 {
		font-size: 3.5rem;
		font-weight: $font-weight-bold;
		color: theme-color(brand-yellow);
		margin-bottom: 2rem;
	}

	p {
		font-size: 1.375rem;
		line-height: 1;
		color: white;

		&:first-of-type {
      font-size: 1.625rem;
			font-weight: $font-weight-bold;
    }
	}

	.btn {
		margin-top: 1rem;
	}
}