// Main Navigation
#site-navigation {
  &.navbar {
    transition: background-color 500ms ease-out 100ms;
    padding-top: 0;
    padding-bottom: 0;
    transition: all 0.5s;

    &.scrollUp {
      transform: translateY(-106px);
    }

    &.darkMenu {
      background-color: rgba(theme-color(brand-grey), 0.85);
    }

    .navbar-collapse {
      @include media-breakpoint-up(lg) {
        width: 50%;

        &.order-2 .navbar-nav {
          margin-left: auto;
        }
      }

      @include media-breakpoint-only(md) {
        justify-content: flex-end !important;

        &.order-lg-0 {
          margin-left: auto;
        }

        &.order-2 {
          flex-grow: 0;
        }
      }

    }

    .navbar-brand {
      width: 188px;
      padding-top: 2.25rem;
      padding-bottom: 2.25rem;

      @include media-breakpoint-up(lg) {
        margin-left: auto !important;
        margin-right: auto !important;
      }

      @include media-breakpoint-only(md) {
        width: 120px;
        margin-left: 0 !important;
      }

      @include media-breakpoint-down(sm) {
        width: 150px;
        padding-top: 1rem;
        padding-bottom: 1rem;
        margin-left: auto !important;
        margin-right: auto !important;
      }
    }

    .navbar-nav {
      .nav-item {


        .nav-link {
          color: white;
          padding-top: 2.5rem;
          padding-bottom: 2.5rem;
          position: relative;

          @include media-breakpoint-up(xl) {
            padding-left: 0.75rem;
            padding-right: 0.75rem;
          }

          @include media-breakpoint-only(md) {
            padding-left: 0.35rem;
            padding-right: 0.35rem;
            font-size:0.9rem;
          }

          // &:before {
          //   content: "";
          //   display: block;
          //   position: absolute;
          //   left: 0;
          //   right: 0;
          //   top: 0;
          //   bottom: 0;
          //   opacity: 0;
          //   @include gradient-y($start-color: rgba(white, 0.5), $end-color: transparent, $start-percent: 0%, $end-percent: 50%);
          //   transition: opacity 0.5s ease;
          // }

          // &:hover:before {
          //   opacity: 1;
          // }
        }

        .btn {
          margin-top: 2rem;
          margin-left: 0.5rem;

          @include media-breakpoint-only(md) {
            padding-left: 1rem;
            padding-right: 1rem;
          }

          &:hover {
            background-color: theme-color(brand-yellow);
            border-color: theme-color(brand-yellow);
            color: white;
          }
        }

        &.active {
          .nav-link {
            position: relative;
            font-weight: $font-weight-bold;

            &:before {
              position: absolute;
              content: '';
              bottom: 2rem;
              left: 0.75rem;
              height: 2px;
              width: 90%;
              /* fallback if needed */
              width: calc(100% - 1.5rem);
              border-bottom: 2px solid white;

              @include media-breakpoint-only(md) {
                left: 0.5rem;
                width: calc(100% - 1rem);
              }
            }
          }
        }
      }
    }
  }
}


.offcanvas-menu-toggle {
  color: white;
  background-color: transparent;
  border: none;
  outline: none;
  box-shadow: none;
  width: 30px;
  height: 20px;
  margin-right: 1rem;
  margin-top: 10px;
  margin-bottom: 10px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;

  &:hover,
  &:focus,
  &:active {
    border: none;
    outline: none;
    box-shadow: none;
  }

  span {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background: white;
    border-radius: 0;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: 0.25s ease-in-out;
    -moz-transition: 0.25s ease-in-out;
    -o-transition: 0.25s ease-in-out;
    transition: 0.25s ease-in-out;

    &:nth-child(1) {
      top: 0px;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }

    &:nth-child(2) {
      top: 9px;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }

    &:nth-child(3) {
      top: 18px;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }
  }

  &.open {
    span {
      &:nth-child(1) {
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
        top: 1px;
        left: 8px;
      }

      &:nth-child(2) {
        width: 0%;
        opacity: 0;
      }

      &:nth-child(3) {
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg);
        top: 21px;
        left: 8px;
      }
    }
  }
}

// Mobile Navigation
/*! Pushy - v1.1.0 - 2017-1-30
* Pushy is a responsive off-canvas navigation menu using CSS transforms & transitions.
* https://github.com/christophery/pushy/
* by Christopher Yee */

$menu_width: 200px !default;
/* Menu Appearance */

.pushy {
  position: fixed;
  width: $menu_width;
  height: 100%;
  top: 0;
  z-index: 7000;
  background: theme-color(brand-grey);
  overflow: auto;
  -webkit-overflow-scrolling: touch;

  // box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25),
  // 0 10px 10px rgba(0, 0, 0, 0.22);
  /* enables momentum scrolling in iOS overflow elements */
  a {
    font-size: 1rem;
    display: block;
    color: white;
    padding: 15px 30px;
    text-decoration: none;
    display: flex;

    span {
      font-size: 1.5rem;
      margin-right: 10px;
    }
  }

  a:hover {
    background-color: theme-color(brand-yellow);
    color: color-yiq(theme-color(brand-yellow));
  }

  a.menu-title:hover {
    cursor: default;
    background-color: #343a40;
    color: color-yiq(#343a40);
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  &.pushy-left {
    left: 0;
  }

  &.pushy-right {
    right: 0;
  }

  .pushy-link {
    &.active {
      // background-color: theme-color(brand-yellow);
      @include gradient-bg(theme-color(brand-yellow));

      a {
        color: color-yiq(theme-color(brand-yellow));
      }
    }
  }
}

.pushy-content {
  visibility: hidden;
}

/* Menu Movement */

.pushy-left {
  -webkit-transform: translate3d(unquote('-'+ $menu_width), 0, 0);
  -ms-transform: translate3d(unquote('-'+ $menu_width), 0, 0);
  transform: translate3d(unquote('-'+ $menu_width), 0, 0);
}

.pushy-open-left {

  #page,
  .push {
    -webkit-transform: translate3d($menu_width, 0, 0);
    -ms-transform: translate3d($menu_width, 0, 0);
    transform: translate3d($menu_width, 0, 0);
  }
}

.pushy-right {
  -webkit-transform: translate3d($menu_width, 0, 0);
  -ms-transform: translate3d($menu_width, 0, 0);
  transform: translate3d($menu_width, 0, 0);
}

.pushy-open-right {

  #page,
  .push {
    -webkit-transform: translate3d(unquote('-'+ $menu_width), 0, 0);
    -ms-transform: translate3d(unquote('-'+ $menu_width), 0, 0);
    transform: translate3d(unquote('-'+ $menu_width), 0, 0);
  }
}

.pushy-open-left,
.pushy-open-right {
  .pushy {
    -webkit-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  .pushy-content {
    visibility: visible;
  }
}

/* Menu Transitions */

#page,
.pushy,
.push {
  transition: transform 0.2s cubic-bezier(0.16, 0.68, 0.43, 0.99);
}

.pushy-content {
  transition: visibility 0.2s cubic-bezier(0.16, 0.68, 0.43, 0.99);
}

/* Site Overlay */

.site-overlay {
  display: none;
}

.pushy-open-left,
.pushy-open-right {
  .site-overlay {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 6668;
    background-color: rgba(255, 255, 255, 0.5);
    -webkit-animation: fade 500ms;
    animation: fade 500ms;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

/* Submenu Appearance */

.pushy-submenu {
  ul {
    background-color: #6c757d;
    transition: max-height 0.2s ease-in-out, visibility 0.2s ease-in-out;

    .pushy-link {
      transition: opacity 0.2s ease-in-out;
    }
  }

  /* Submenu Buttons */
  button {
    font-weight: bold;
    font-size: 0.85rem;
    text-transform: uppercase;
    letter-spacing: 2px;
    width: 100%;
    color: white;
    padding: 15px 30px 15px 30px;
    text-align: left;
    background: transparent;
    border: 0;
    outline: none;
    box-shadow: none;
    display: flex;

    &:hover {
      color: white;
      background-color: theme-color(brand-yellow);
    }

    span {
      margin-right: 10px;
      font-size: 1.5rem;
      align-self: center !important;
    }
  }

  /* Submenu Icon */
  a {
    font-weight: normal;
    padding: 15px 30px 15px 67px;
    text-transform: none;
    letter-spacing: 0;
  }

  >a,
  >button {
    position: relative;
  }

  >button::after {
    display: block;
    height: 11px;
    width: 8px;
    position: absolute;
    top: 30%;
    right: 15px;
    font-family: 'Font Awesome 5 Pro';
    content: '\f0d7';
  }
}

/* Submenu Movement */

.pushy-submenu-closed {
  ul {
    max-height: 0;
    overflow: hidden;
    visibility: hidden;
  }

  .pushy-link {
    opacity: 0;
  }
}

.pushy-submenu-open {
  ul {
    max-height: 1000px;
    visibility: visible;
  }

  .pushy-link {
    opacity: 1;
  }

  button {
    background-color: #6c757d;
    color: color-yiq(#6c757d);
  }

  /* Submenu Icon */
  button::after {
    font-family: 'Font Awesome 5 Pro';
    content: '\f0d8';
  }
}

.pushy .footer-links {
  margin-top: 2rem;
  border-top: 1px solid black;

  a {
    font-size: 0.8rem;
    color: #6c757d;
    text-transform: none;
    letter-spacing: 0;
  }
}