.site-footer {
   background-color: theme-color(brand-grey);
   padding-top: 2.5rem;
   padding-bottom: 2.5rem;
   .logo {
      width: 163px;
      display: inline-block;
      @include media-breakpoint-down(md) {
         margin-bottom: 1rem;
      }
   }
   ul.nav {
      .nav-item {
         .nav-link {
            color: white;
            &:hover {
               color: theme-color(brand-yellow);
            }
            @include media-breakpoint-down(md) {
               padding-left:0.5rem;
               padding-right:0.5rem;
            }
            @include media-breakpoint-down(sm) {
               font-size: 0.75rem;
            }
         }
      }
   }
   ul.legal {
      list-style-type: none;
      margin:0;
      padding:0;
      width:100%;
      display: flex;
      justify-content: center;
      li {
         padding:1rem;
         color:theme-color(brand-grey-light);
         font-size:0.75rem;
         a {
            color:theme-color(brand-grey-light);
            text-decoration: none;
         }
         
      }
      @include media-breakpoint-down(md) {
         li {
            padding:0.5rem;
         }
      }
      @include media-breakpoint-down(sm) {
         flex-direction: column;
         li {
            padding:0.25rem;
         }
      }
   }
}